input[type="text"],
select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

input[type="submit"] {
  width: 25%;
  // background-color: #4caf50;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type="submit"]:hover {
  background-color: #45a049;
}

div {
  width: 75%;
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}

.intro {
  width: 75%;
  border-radius: 5px;
  padding: 20px;
  background-color: white;
}

mark {
  background: red;
  color: white;
}
